/** @jsxImportSource @emotion/react */
import { CustomTheme } from "src/theme";
import { css, useTheme } from "@emotion/react";
import { useState, useRef, useEffect } from "react";
import domtoimage from "dom-to-image";
import html2canvas from "html2canvas";

import { Template } from "@components/feature/template/Template";
import { Generator } from "@components/feature/generator/Generator";
import { Preview } from "@components/feature/preview/Preview";
import { Done } from "@components/feature/done/Done";
import { useIsDone } from "@hooks/useIsDone";
import { Modal } from "@components/ui/modal/Modal";
import { ButtonText } from "@components/ui/text/Text";

export default function GeneratePage() {
  const [template, setTemplate] = useState<TTemplate>(null);
  const [image, setImage] = useState<string | null>(null);
  const [logo, setLogo] = useState<string | null>(null);
  const [content, setContent] = useState<Tcontent>({ title: null, desc: null });
  const [isModalOpen, setIsModalOpen] = useState(false);
  const imageRef = useRef<HTMLInputElement>(null);
  const logoRef = useRef<HTMLInputElement>(null);
  const captureRef = useRef<HTMLDivElement>(null);
  const [isDownloadModalOpen, setIsDownloadModalOpen] = useState(false);
  const downloadModalRef = useRef<HTMLDivElement>(null);
  const [capturedImage, setCapturedImage] = useState<string | null>(null);
  const press_image_to_download_ = "press image to download";

  const { handleIsAllDone } = useIsDone();

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const imageUrl = URL.createObjectURL(file);
      setImage(imageUrl);
    }
  };

  const handleImageDelete = () => {
    if (imageRef.current) {
      imageRef.current.value = "";
    }
    setImage(null);
  };

  const handleImageClick = () => {
    imageRef.current?.click();
  };

  const handleLogoChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const imageUrl = URL.createObjectURL(file);
      setLogo(imageUrl);
    }
  };

  const handleLogoDelete = () => {
    if (logoRef.current) {
      logoRef.current.value = "";
    }
    setLogo(null);
  };

  const handleLogoClick = () => {
    logoRef.current?.click();
  };

  const handleTitleChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setContent((prev: Tcontent) => ({
      ...prev,
      title: e.target.value,
    }));
  };

  const handleDescChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setContent((prev: Tcontent) => ({
      ...prev,
      desc: e.target.value,
    }));
  };

  const handleCancelContent = () => {
    setIsModalOpen(false);
    setContent({ title: null, desc: null });
  };

  const handleSaveContent = () => {
    setIsModalOpen(false);
  };

  const forceRedraw = (element: HTMLElement) => {
    element.style.display = "none";
    void element.offsetHeight; //   
    element.style.display = "";
  };

  const prepareForCapture = () => {
    if (captureRef.current) {
      captureRef.current.style.transform = "none";
      captureRef.current.style.filter = "none";
      captureRef.current.style.opacity = "1";
    }
  };

  const waitForImages = (element: HTMLElement) => {
    return new Promise<void>((resolve) => {
      const images = Array.from(element.querySelectorAll("img"));
      const backgroundImages = Array.from(element.querySelectorAll("*"))
        .map((el) => window.getComputedStyle(el).backgroundImage)
        .filter((url) => url.startsWith("url(")); //  background-image   
      let loadedImages = 0;

      if (images.length === 0) {
        resolve();
        return;
      }

      const checkImageLoaded = (
        img: HTMLImageElement | HTMLDivElement,
        src: string
      ): Promise<boolean> => {
        return new Promise((resolve) => {
          const image = new Image();
          image.src = src;
          image.crossOrigin = "anonymous"; //  CORS  

          image.onload = () => {
            console.log(`   : ${src}`);
            resolve(true);
          };
          image.onerror = () => {
            console.log(`   : ${src}`);
            resolve(false);
          };
        });
      };

      //  `<img>`  
      const imgLoadPromises = images.map((img) =>
        checkImageLoaded(img, img.src)
      );

      //  `background-image` 
      const bgLoadPromises = backgroundImages.map((bgUrl) => {
        const src = bgUrl.replace(/^url\(["']?|["']?\)$/g, ""); //  URL `url("...")`  
        return checkImageLoaded(document.createElement("div"), src);
      });

      //    &      
      Promise.all([...imgLoadPromises, ...bgLoadPromises]).then((results) => {
        const loadedCount = results.filter((status) => status === true).length;
        console.log(
          `   : ${loadedCount} / ${
            images.length + backgroundImages.length
          }`
        );
        resolve();
      });

      //  `<img>`  `loading="eager"` 
      images.forEach((img) => {
        img.loading = "eager"; //  lazy-loading 
        img.crossOrigin = "anonymous"; //  CORS  
      });
    });
  };

  // const waitForRender = (): Promise<void> => {
  //   return new Promise((resolve) => {
  //     setTimeout(() => {
  //       requestAnimationFrame(() => {
  //         requestAnimationFrame(() => {
  //           resolve();
  //         });
  //       });
  //     }, 300);
  //   });
  // };

  const handleCapture = async () => {
    if (!captureRef.current) return;

    await waitForImages(captureRef.current);

    setTimeout(async () => {
      const canvas = await html2canvas(captureRef.current as HTMLDivElement, {
        scale: 2, //  2  
        useCORS: true, //    
        backgroundColor: null, //   
      });

      const imgData = canvas.toDataURL("image/png");
      console.log("   :", imgData ? "" : "");

      if (imgData) {
        setCapturedImage(imgData);
      }
    }, 500); //     500ms    
  };
  // const imgData = await domtoimage.toPng(captureRef.current);
  // const isMobile = /iPhone|iPad|Android/i.test(navigator.userAgent);

  // if (isMobile) {
  //   if (imgData) {
  //     setCapturedImage(imgData);
  //   }
  // } else {
  //   const link = document.createElement("a");
  //   link.href = imgData;
  //   link.download = "captured-image.png";
  //   document.body.appendChild(link);
  //   link.click();
  //   document.body.removeChild(link);
  // }

  const handleCloseModal = () => {
    setIsDownloadModalOpen(false);
    setCapturedImage(null);
  };

  useEffect(() => {
    if (capturedImage) {
      setIsDownloadModalOpen(true);
    }
  }, [capturedImage]);

  useEffect(() => {
    handleImageDelete();
    handleCancelContent();
  }, [template]);

  const theme = useTheme() as CustomTheme;

  const container = (theme: CustomTheme) => css`
    display: flex;
    flex-direction: column;
  `;

  const captured_image = css`
    width: 100%;
    max-width: 720px;
  `;

  const download_info_container = css`
    width: 100%;
    display: flex;
    justify-content: center;
  `;

  return (
    <div css={container(theme)}>
      <Template template={template} setTemplate={setTemplate} />
      {template && (
        <>
          <Generator
            template={template}
            logo={logo}
            handleLogoClick={handleLogoClick}
            handleLogoChange={handleLogoChange}
            handleLogoDelete={handleLogoDelete}
            logoRef={logoRef}
            image={image}
            handleImageClick={handleImageClick}
            handleImageChange={handleImageChange}
            handleImageDelete={handleImageDelete}
            imageRef={imageRef}
            content={content}
            isModalOpen={isModalOpen}
            setIsModalOpen={setIsModalOpen}
            handleTitleChange={handleTitleChange}
            handleDescChange={handleDescChange}
            handleCancelContent={handleCancelContent}
            handleSaveContent={handleSaveContent}
          />
          <Preview
            template={template}
            logo={logo}
            image={image}
            content={content}
            ref={captureRef}
          />
          {handleIsAllDone(content, template, logo, image) && (
            <Done isDone={true} handleCapture={handleCapture} />
          )}
        </>
      )}
      <Modal
        isOpen={isDownloadModalOpen}
        setIsOpen={setIsDownloadModalOpen}
        handleCloseModal={handleCloseModal}
      >
        <div ref={downloadModalRef}>
          {capturedImage && (
            <img src={capturedImage} alt="Captured" css={captured_image} />
          )}
        </div>
        <div css={download_info_container}>
          <ButtonText text={press_image_to_download_} />
        </div>
      </Modal>
    </div>
  );
}
